<template>
  <div >
     <div style="margin-top: 10px;display: flex;justify-content: center">
      <el-input
        placeholder="..."
        prefix-icon="el-icon-search"
        v-model="keywords" style="width: 400px" size="small">
      </el-input>
      <el-button type="primary" icon="el-icon-search" size="small" style="margin-left: 3px" @click="searchClick">
      </el-button>
      <el-button type="primary" size="mini" icon="el-icon-refresh" @click="refreshUserList"> </el-button>
       <el-button type="primary" size="mini" icon="el-icon-plus" @click="showUserForm"> </el-button>
    </div>
<!--       -->
 <div style="display: flex;justify-content: space-around;flex-wrap: wrap">
      <el-card style="width:330px;margin-top: 10px;" v-for="(user,index) in users" :key="index"
               v-loading="cardloading[index]">
        <div slot="header" style="text-align: left">
          <span>{{user.nickname}}</span>
          <el-button style="float: right; padding: 3px 0;color: #green" type="text" icon="el-icon-edit"
                     @click="updatePassword(user.id)">
          </el-button>
          <el-button style="float: right; padding: 3px 0;color: #green" type="text" icon="el-icon-document"
                     @click="updateUser(user.id)">
          </el-button>
          <el-button style="float: right; padding: 3px 0;color: #ff0509" type="text" icon="el-icon-delete"
                     @click="deleteUser(user.id)">
          </el-button>
          
        </div>
        <div>
          <div><img :src="user.avatar"  style="width: 70px;height: 70px"></div>
          <div style="text-align: left;color:#20a0ff;font-size: 12px;margin-top: 13px">
            <span>:</span><span>{{user.username}}</span>
          </div>
          <div style="text-align: left;color:#20a0ff;font-size: 12px;margin-top: 13px">
            <span>:</span><span>{{user.email}}</span>
          </div>
          <div style="text-align: left;color:#20a0ff;font-size: 12px;margin-top: 13px">
            <span>:</span><span>{{user.reg_date}}</span>
          </div>
          <div
            style="text-align: left;color:#20a0ff;font-size: 12px;margin-top: 13px;display: flex;align-items: center">
            <span>:</span>
            <el-switch
              v-model="user.status"
               active-text=""
               active-color="#13ce66"
               active-value="0"
               inactive-value="1"
              @change="enabledChange(user.status,user.id,index)"
              inactive-text="" style="font-size: 12px">
            </el-switch>
          </div>
          <div style="text-align: left;color:#20a0ff;font-size: 12px;margin-top: 13px">
            <span>:</span>
            <el-tag
              v-for="role in user.authRoles"
              :key="role.id"
              size="mini"
              style="margin-right: 8px"
              type="success">
              {{role.name}}
            </el-tag>
            <el-popover
              placement="right"
              title=""
              width="200"
              :key="index+''+user.id"
               @hide="saveRoles(user.id,index)"
              trigger="click" v-loading="eploading[index]">
              <el-select v-model="roles" :key="user.id" multiple placeholder="" size="mini">
                <el-option
                  v-for="(item,index) in allRoles"
                  :key="user.id+'-'+item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
              <el-button type="text" icon="el-icon-more" style="padding-top: 0px" slot="reference"
                         @click="showRole(user.authRoles,user.id,index)"></el-button>
            </el-popover>
          </div>
        </div>
      </el-card>

  </div>
  <!--  -->
                     <div style="text-align: right;margin-top: 10px">
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[3, 6, 9]"
                            :page-size= pageSize
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="totalCount">
                        </el-pagination>
                    </div> 

<!--   -->
<el-form :model="userForm" :rules="rules" ref="addUserForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog
          :title="dialogTitle"
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible"
          width="40%">
          <el-row>
            <el-col :span="12">
                <el-form-item label=":" prop="nickname">
                  <el-input  v-model="userForm.nickname" size="mini" style="width: 150px"
                            placeholder=""></el-input>
                </el-form-item>
            </el-col>
           
           <el-col :span="11" style="padding-left:10px">
                <el-form-item label=":" prop="username">
                  <el-input  v-model="userForm.username" size="mini" style="width: 150px"
                            placeholder=""></el-input>
                </el-form-item>
            </el-col>
          </el-row>

          <el-row>
  
            <el-col :span="12">
                <el-form-item label=":" prop="sex">
                    <el-select v-model="userForm.sex" placeholder="" style="width:150px;" size="mini">
                        <el-option
                          v-for="item in sexOptions"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-col>


             <el-col :span="11" style="padding-left: 35px">
                <el-form-item label=":" >
                  <el-switch
                              v-model="userForm.status"
                              active-color="#13ce66"
                              inactive-color="#ff4949"
                              active-value="0"
                              inactive-value="1"
                              >
                  </el-switch>
              </el-form-item>
           </el-col>
          </el-row>

        <el-row>
            <el-col :span="12">
                 <el-form-item label=":" prop="email">
                  <el-input prefix-icon="el-icon-message" v-model="userForm.email" size="mini" style="width: 150px"
                            placeholder=""></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label=":" prop="mobile">
                  <el-input prefix-icon="el-icon-phone" v-model="userForm.mobile" size="mini" style="width: 150px"
                            placeholder="" ></el-input>
                </el-form-item>
            </el-col>
          </el-row>
        <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="resetUser"> </el-button>
        <el-button size="mini" type="primary" @click="saveUser('addUserForm',userForm.id)"> </el-button>
      </span>
    </el-dialog>
  </div>
</el-form>

<!--  -->
<el-form :model="passWordForm" :rules="passwordRules" ref="passWordForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: center">
        <el-dialog
          :title="dialogTitle"
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="paswordDialogVisible"
          width="30%">

             <el-form-item label=":" prop="oldPassword" style="padding-left:0px">
             <el-input type="text" v-model="passWordForm.oldPassword" autocomplete="off"  placeholder="" size="mini" style="width: 150px;padding-left:15px;float:left"></el-input>
            </el-form-item>
         
            <el-form-item label=":" prop="newPassWord">
            <el-input type="password" v-model="passWordForm.newPassWord" autocomplete="off" placeholder=""  size="mini" style="width: 150px;padding-left:15px;float:left"></el-input>
           </el-form-item>
           
             <el-form-item label=":" prop="checkPassPassword">
             <el-input type="password" v-model="passWordForm.checkPassPassword" autocomplete="off"  placeholder=""   size="mini" style="width: 150px;padding-left:0px;float:left"></el-input>
             </el-form-item>
      
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="cancerPasswordForm"> </el-button>
        <el-button size="mini" type="primary" @click="submitPasswordForm('passWordForm',passWordForm.id)"> </el-button>
      </span>
    </el-dialog>
  </div>
</el-form>
  </div>
</template>


<script>

export default {
   // html
  mounted: function () {
      this.loading = true;
      //
      this.loadUserList();
      this.cardloading = Array.apply(null, Array(20)).map(function (item, i) {
        return false;
      });
      this.eploading = Array.apply(null, Array(20)).map(function (item, i) {
        return false;
      });
    },

  data(){

    var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.passWordForm.newPassWord !== '') {
            this.$refs.passWordForm.validateField('checkPassPassword');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.passWordForm.newPassWord) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };


    return{
      // 
        loading: false,
        eploading: [],
        cardloading: [],
        // 
        keywords: '',
        // 
        users: [],
        allRoles: [],
        roles: [],
        cpRoles: [],
        // 
        userForm:{
           username:"",
           nickname:"",
           sex:"",
           mobile:"",
           email:"",
           id:"",
           status:"0"
        }
        ,sexOptions:[
          {
          value: '0',
          label: ''
        }, {
          value: '1',
          label: ''
        }
        ],
        // 
        totalCount: 0,
        currentPage: 1,
        pageSize: 3,
        dialogTitle:"",
        // 
        dialogVisible: false,
        // 
        paswordDialogVisible : false,
        passWordForm:{
        oldPassword: "",
        newPassWord:"",
        checkPassPassword :"",
        id:""
        },
        // 
        rules: {
          nickname:   [{required: true, message: ':', trigger: 'blur'}],
          username:   [{required: true, message: ':', trigger: 'blur'}],
          sex: [{required: true, message: ':', trigger: 'blur'}],
          email:  [{required: true, message: ':', trigger: 'blur'}, {
            type: 'email',
            message: '',
            trigger: 'blur'
          }],
          mobile:  [{required: true, message: ':', trigger: 'blur'}],
        }
        ,passwordRules: {
           newPassWord: [{ validator: validatePass, trigger: 'blur' }],
           checkPassPassword: [{ validator: validatePass2, trigger: 'blur' }],
        }

    }
  },
  methods:{
    // 
    loadUserList(){
      this.loading = true;
      this.$getRequest('/user/list', { 
        nickname: this.keywords,
        pageSize: this.pageSize,
        currentPage: this.currentPage
        }
         ).then(result=> {
          this.loading = false;
          if (result && result.status == 200) {
            this.users = result.data.data;
            this.totalCount = result.data.total;
          }else {
            this.$message({type: 'error', message: '!'});
          }
        });
    },
    // 
     showRole(aRoles, id, index){
        this.cpRoles = aRoles;
        this.roles = [];
        this.loadRoles(index);
        for (var i = 0; i < aRoles.length; i++) {
          this.roles.push(aRoles[i].id);
        }
      },
       //
       loadRoles(index){
          this.eploading.splice(index, 1, true)
          this.$getRequest('/role/list' ).then(result=> {
          this.eploading.splice(index, 1, false)
          if (result && result.status == 200) {
            this.allRoles = result.data.data;
          }else {
            this.$message({type: 'error', message: '!'});
          }
        });
      },
   // 
    saveRoles(id, index){
       var selRoles = this.roles;
        if (this.cpRoles.length == selRoles.length) {
          for (var i = 0; i < this.cpRoles.length; i++) {
            for (var j = 0; j < selRoles.length; j++) {
              if (this.cpRoles[i].id == selRoles[j]) {
                selRoles.splice(j, 1);
                break;
              }
            }
          }
          if (selRoles.length == 0) {
            return;
          }
        }
          this.cardloading.splice(index, 1, true)
          this.$postRequest("/user/saveUserRoles", {roleIds: this.roles, userId: id}).then(resp=> {
          if (resp.data.status == 200) {
             this.$message({type: "success", message: resp.data.message});
              this.loadOneUserById(id,index)
          } else {
            this.cardloading.splice(index, 1, false)
            this.$message({type: 'error', message: '!'});
          }
        }, resp=> {
          this.cardloading.splice(index, 1, false)
            this.$message({type: 'error', message: ""});
        })
    },
    // 
     loadOneUserById(id, index){
        this.$getRequest("/user/getUserById",{id:id}).then(resp=> {
            this.cardloading.splice(index, 1, false)
          if (resp.data.status == 200) {
            this.users.splice(index, 1, resp.data.data);
          } else {
            this.$message({type: 'error', message: '!'});
          }
        }, resp=> {
          this.cardloading.splice(index, 1, false)
          this.$message({type: 'error', message: ""});
        });
      },
      // 
       searchClick(){
        this.loading = true;
        this.loadUserList();
      }
      // 
      ,deleteUser(id){
         var _this = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          _this.loading = true;
          _this.$deleteRequest("/user/delete" , {id:id}).then(resp=> {
            if (resp.data.status ==  200) {
              _this.$message({type: 'success', message: resp.data.message})
              _this.loadUserList();
              return;
            }else{
               _this.loading = false;
               _this.$message({type: 'error', message: resp.data.message})
            }
        
          }, resp=> {
            _this.loading = false;
            _this.$message({type: 'error', message: '!'})
          });
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          });
        });
      },
     // 
    enabledChange(status, id, index){
        var _this = this;
        _this.cardloading.splice(index, 1, true)
        _this.$putRequest("/user/updateStatus", {status: status, id: id}).then(resp=> {
          if (resp.data.status != 200) {
            _this.$message({type: 'error', message: resp.data.message})
            _this.loadOneUserById(id, index);
            return;
          }
          _this.cardloading.splice(index, 1, false)
          _this.$message({type: 'success', message: resp.data.message})
        }, resp=> {
          _this.$message({type: 'error', message: '!'})
          _this.loadOneUserById(id, index);
        });
      },
      // 
        showUserForm(){
               this.dialogTitle = "";
                this.dialogVisible = true;
                var _this = this;
        }
        // 
        , resetUser() {
          this.dialogVisible = false;
          this.emptyUserFormData();
          this.loadUserList();
      },
      // 
      emptyUserFormData(){
          this.userForm={
                    username:"",
                    nickname:"",
                    sex:"",
                    mobile:"",
                    email:"",
                    id:"",
                    status:"0"
                }
       }
       // 
       ,saveUser(formName,id){
           this.$refs[formName].validate((valid) => {
               if (valid) {
                 this.loading = true;
                 this.$postRequest('/user/saveUser',this.userForm) 
                 .then(result =>{
                  this.loading = false;
                 if(result.data.status == 200){
                      this.dialogVisible = false;
                      this.$message({type: 'success', message: result.data.message})    
                      this.emptyUserFormData();
                      this.loadUserList();              
                  }else{
                      this.$message({type: 'error', message: result.data.message})
                  }
               
                 })
               }
         })
       }
       // 
       ,updateUser(id){
            this.dialogTitle = "";
            this.loading = true;
            this.$getRequest('/user/getOneUser',{id:id})
            .then(result =>{
                this.dialogVisible = true;
                this.loading = false;
                 if(result.data.status == 200){
                    this.userForm = result.data.data
                  }else{
                      this.$message({type: 'error', message: ""})
                  }
               
                 })
          }
          // 
          ,updatePassword(id){
               this.dialogTitle = "";
               this.paswordDialogVisible =true;
               this.passWordForm.id = id;
          }
          // 
          ,submitPasswordForm(formName,id){
               this.$refs[formName].validate((valid) => {
                  if (valid) {
                      this.$putRequest('/user/updateUserPassword',this.passWordForm)
                        .then(result =>{
                          this.paswordDialogVisible = false;
                          this.passWordForm = {};
                         if(result.data.status == 200){
                             this.$message({type: 'success', message: result.data.message})
                             const currentUserId = this.$db.get('user').id;
                             //  
                            if(currentUserId == id){
                                     this.$router.replace({path: '/'});
                                   // 
                                    this.$db.clear();
                              }
                        }else{
                            this.$message({type: 'error', message: result.data.message})
                        }
                     })
                  }
               })
          }
          ,cancerPasswordForm(){
                this.paswordDialogVisible = false;
                this. passWordForm = {
                    oldPassword: "",
                    newPassWord:"",
                    checkPassPassword :""
                }
          }
          // 
        ,handleSizeChange(val){
           this.pageSize = val;
           this.loadUserList();
        }
        // 
        ,handleCurrentChange(val){
           this.currentPage = val;
           this.loadUserList();
        }
        // 
        ,refreshUserList(){
          this.keywords = "";
           this.loadUserList();
        }
    }
}
</script>