<template>
  <div>


<div style="margin-top: 20px;display: flex;justify-content: left">
      <el-input
        placeholder="..."
        prefix-icon="el-icon-search"
        v-model="searchParams.username" style="width: 200px" size="small">
      </el-input>

      <el-date-picker size="small"
      v-model="time"
      type="daterange"
      range-separator=""
      start-placeholder=""
      end-placeholder="">
    </el-date-picker>

      <el-button type="primary" icon="el-icon-search" size="small" style="margin-left: 3px" @click="search">
      </el-button>
       <el-button type="primary" icon="el-icon-refresh" size="small" style="margin-left: 3px" @click="refresh">
      </el-button>
    
    </div>

    <div style="margin-top: 10px">
 <el-table
      :data="logList"
      size="mini"
      border
      stripe
      v-loading="tableLoading"
      style="width: 100%">
       <el-table-column  type="selection"   align="center"  width="55"></el-table-column>
       <el-table-column  type="index"  align="center" fixed label="" width="50"> </el-table-column>
       <el-table-column prop="nickname" align="center" fixed  label=""   width="100"> </el-table-column>
       <el-table-column prop="username" align="center" fixed  label=""   width="100"> </el-table-column>
       <el-table-column prop="actionDescription" align="left" fixed  label=""   width="100"> </el-table-column>
       <el-table-column prop="className" align="center" fixed :show-overflow-tooltip="true" label=""   width="100"> </el-table-column>
       <el-table-column prop="methodName" align="center" fixed :show-overflow-tooltip="true" label=""   width="100"> </el-table-column>
        <el-table-column prop="params" align="center" :show-overflow-tooltip="true" fixed  label=""   width="100"> </el-table-column>
        <el-table-column prop="ip" align="center" fixed  label="ip"   width="100"> </el-table-column>
        <el-table-column prop="operateType" align="center" fixed  label=""   width="100"> </el-table-column>
       <el-table-column  prop="createTime" align="center" fixed  label=""   width="140"> </el-table-column> 

    <el-table-column label="" align="center">
        <template slot-scope="scope">
                  <el-button
                    size="mini"
                    class="el-icon-delete" type="danger"
                    @click="deleteLog(scope.$index, scope.row)">
                  </el-button>
               </template>
     </el-table-column>
    </el-table>
</div>

   <div style="text-align: right;margin-top: 10px">
       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 15]"
        :page-size= pageSize
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
    </el-pagination>
    </div> 

  </div> 
</template>



<script>
export default {
   data(){
    return{
            searchParams:{
                    username:"",
                    startTime:"",
                    endTime:"",
                    type:""
            }, 
            time:"",
            logList:[],
            tableLoading: false,
            totalCount: 0,
            currentPage: 1,
            pageSize: 5,
    }
   },methods:{
       // 
        search(){
          this.loadLogTable();
        }
        // 
        ,refresh(){
          this.searchParams = {
            username:"",
          };
          this.loadLogTable();
        }
        // 
        ,deleteLog(){

        },
            // 
        handleSizeChange(val){
            this.pageSize = val;
            this.loadLogTable();
        },
        // 
        handleCurrentChange(val){
            this.currentPage = val;
            this.loadLogTable();
        },
        // 
        loadLogTable(){
           this.tableLoading = true;
              this.searchParams.startTime = this.getTime(new Date(this.time[0]));
              this.searchParams.endTime = this.getTime(new Date(this.time[1]));
          
           this.$getRequest('/log/selectLogWithPage', { 
      
            pageSize: this.pageSize,
            currentPage: this.currentPage
        }).then(result=> {
            this.tableLoading = false;
            if (result && result.status == 200) {
                this.logList = result.data.data;
                this.totalCount = result.data.total;
            }else {
                this.$message({type: 'error', message: '!'});
            }
        });
        } ,
        //
        getTime(dt){
           if(dt){
          var year = dt.getFullYear(); //
            var month = dt.getMonth() +1; //
            var date = dt.getDate(); //
            month = month < 10 ? "0" + month : month;
            date  = date <10 ? "0" + date : date;
            var str = year + "-" + month + "-" + date;
            return str;
           }else{
             return "";
           }
        }

   },mounted(){
      this.loadLogTable();
   }    
}
</script>
